.button {
  background-color: rgba(255, 255, 255, 0.16);
  border: 0;
  color: #ffffff;
  font-size: 18px;
  padding: 10px 35px;
  border-radius: 6px;
  cursor: pointer;
}

.button--large {
  font-size: 32px;
  padding: 15px 45px;
}

.button:disabled {
  cursor: default;
}


