.app {
  height: 100%;
  background: url(http://www.script-tutorials.com/demos/360/images/stars.png)
    repeat top center;
}

.mars-surface {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  pointer-events: none;
}

.astronaut {
  position: absolute;
  right: 10%;
  top: 10%;
  -webkit-animation: floating 50s infinite;
          animation: floating 50s infinite;
  z-index: -1;
}

.spaceship {
  position: absolute;
  left: 5%;
  top: 5%;
  -webkit-animation: flying 15s linear infinite;
          animation: flying 15s linear infinite;
}

.spaceship__fire {
  position: absolute;
  bottom: 11px;
  left: 9px;
  -webkit-transform: rotate(4deg);
      -ms-transform: rotate(4deg);
          transform: rotate(4deg);
  -webkit-animation: fire 0.2s linear infinite;
          animation: fire 0.2s linear infinite;
}

@-webkit-keyframes floating {
  from {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
  50% {
    -webkit-transform: rotate(-10deg) translateY(60px) scale(0.4);
            transform: rotate(-10deg) translateY(60px) scale(0.4);
  }
  to {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
}

@keyframes floating {
  from {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
  50% {
    -webkit-transform: rotate(-10deg) translateY(60px) scale(0.4);
            transform: rotate(-10deg) translateY(60px) scale(0.4);
  }
  to {
    -webkit-transform: rotate(20deg) translateY(0px) scale(1);
            transform: rotate(20deg) translateY(0px) scale(1);
  }
}

@-webkit-keyframes flying {
  0% {
    -webkit-transform: translate(-200px, 200px) rotate(-10deg);
            transform: translate(-200px, 200px) rotate(-10deg);
  }
  20% {
    -webkit-transform: translate(100px, -200px) rotate(-10deg);
            transform: translate(100px, -200px) rotate(-10deg);
  }
  100% {
    -webkit-transform: translate(100px, -200px) rotate(-10deg);
            transform: translate(100px, -200px) rotate(-10deg);
  }
}

@keyframes flying {
  0% {
    -webkit-transform: translate(-200px, 200px) rotate(-10deg);
            transform: translate(-200px, 200px) rotate(-10deg);
  }
  20% {
    -webkit-transform: translate(100px, -200px) rotate(-10deg);
            transform: translate(100px, -200px) rotate(-10deg);
  }
  100% {
    -webkit-transform: translate(100px, -200px) rotate(-10deg);
            transform: translate(100px, -200px) rotate(-10deg);
  }
}

@-webkit-keyframes fire {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fire {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}



